import React from "react";
import { Link } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBIcon,
} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";

export default function Step15(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;

  return (
    <Step heading={`What's your title?`}>
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <MDBContainer>
        <MDBRow center>
          <MDBCol sm={10} md={8}>
            <div className="my-4" />
            <MDBRow center>
              <MDBCol lg={3}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    onChangeField(e, "title", "Owner");
                    history.push(nextStep);
                  }}
                >
                  Owner
                </MDBBtn>
              </MDBCol>
              <MDBCol className="d-block d-lg-none my-2" />
              <MDBCol lg={3}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    onChangeField(e, "title", "CEO");
                    history.push(nextStep);
                  }}
                >
                  CEO
                </MDBBtn>
              </MDBCol>
              <MDBCol className="d-block d-lg-none my-2" />
              <MDBCol lg={3}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    onChangeField(e, "title", "COO");
                    history.push(nextStep);
                  }}
                >
                  COO
                </MDBBtn>
              </MDBCol>{" "}
              <MDBCol className="d-block d-lg-none my-2" />
              <MDBCol lg={3}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    onChangeField(e, "title", "Accountant");
                    history.push(nextStep);
                  }}
                >
                  Accountant
                </MDBBtn>
              </MDBCol>
            </MDBRow>
          </MDBCol>
        </MDBRow>
      </MDBContainer>
    </Step>
  );
}
