import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBIcon,
} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";

export default function Step14(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const formRef = new React.createRef();

  const validate = () => {
    setTouched(true);
    const isValid = formRef.current.checkValidity();

    setErrorMsg(!isValid ? "Please enter valid EIN" : "");
  };

  useEffect(() => {
    setTouched(formRef.current.checkValidity());
  }, []);
  return (
    <Step
      heading={`What's your EIN? P.S. This would be your social if you're a sole proprietorship.`}
    >
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <MDBContainer>
        <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
          <MDBRow center>
            <MDBCol sm={10} md={8}>
              <MDBRow center>
                <MDBCol>
                  <MDBInput
                    label="EIN"
                    group
                    type="tel"
                    name="ein"
                    value={state.ein}
                    onChange={(e) => {
                      if (e.target.value.length > 10) return;
                      onChangeField(
                        e,
                        e.target.name,
                        normalizeInput(e.target.value, state.ein)
                      );
                      validate();
                    }}
                    pattern=".{2}-.{7}"
                    required
                    containerClass="mb-0"
                  />
                </MDBCol>
              </MDBRow>
              <MDBRow center className="d-flex flex-column align-items-center">
                {touched && errorMsg.length > 0 && (
                  <div className="text-danger mb-2">{errorMsg}</div>
                )}
                {touched && !errorMsg && (
                  <Link to={nextStep}>
                    <MDBBtn
                      color="pink"
                      rounded
                      type="button"
                      className="z-depth-1a"
                    >
                      Next
                    </MDBBtn>
                  </Link>
                )}
                {(!touched || errorMsg) && (
                  <MDBBtn
                    color={"grey"}
                    disabled
                    rounded
                    type="button"
                    className="z-depth-1a"
                  >
                    Next
                  </MDBBtn>
                )}
              </MDBRow>
            </MDBCol>
          </MDBRow>
        </form>
      </MDBContainer>
    </Step>
  );
}

const normalizeInput = (value, previousValue) => {
  if (!value) return value;
  const currentValue = value.replace(/-/g, "");
  if (currentValue.length > 2) {
    return currentValue.substring(0, 2) + "-" + currentValue.substring(2);
  } else if (currentValue.length === 2) {
    return currentValue;
  }
  return value;
};
